		function create_attribId1(blink_on_hover_with_instances: GeoObjNode) {
			var attribId1 = blink_on_hover_with_instances.createNode('attribId');
			attribId1.setName('attribId1');
			attribId1.uiData.setPosition(200, 150);
			attribId1.p.idn.set(false);
			attribId1.params.postCreateSpareParams();
			attribId1.params.runOnSceneLoadHooks();
			return attribId1;
		}
		function create_eventsNetwork1(blink_on_hover_with_instances: GeoObjNode) {
			var eventsNetwork1 = blink_on_hover_with_instances.createNode('eventsNetwork');
			eventsNetwork1.setName('eventsNetwork1');
			function create_nodeCook1(eventsNetwork1: EventsNetworkSopNode) {
				var nodeCook1 = eventsNetwork1.createNode('nodeCook');
				nodeCook1.setName('nodeCook1');
				nodeCook1.uiData.setPosition(-400, 150);
				nodeCook1.p.mask.set('*instance*');
				nodeCook1.params.postCreateSpareParams();
				nodeCook1.params.runOnSceneLoadHooks();
				return nodeCook1;
			}
			function create_pointer1(eventsNetwork1: EventsNetworkSopNode) {
				var pointer1 = eventsNetwork1.createNode('pointer', {
					paramsInitValueOverrides: {
						pointerdown: {raw_input: false},
						pointermove: {raw_input: true},
					},
				});
				pointer1.setName('pointer1');
				pointer1.uiData.setPosition(-350, -50);
				// pointer1.p.pointerdown.set(false);
				// pointer1.p.pointermove.set(true);
				pointer1.params.postCreateSpareParams();
				pointer1.params.runOnSceneLoadHooks();
				return pointer1;
			}
			function create_raycast1(eventsNetwork1: EventsNetworkSopNode) {
				var raycast1 = eventsNetwork1.createNode('raycast');
				raycast1.setName('raycast1');
				raycast1.uiData.setComment(
					'This raycasting node is the key to this "blinking on hover" effect. Each time it is triggered (in this case, on pointermove events), it will render the scene using the given material on a single pixel under the cursor. It will then used the value of that pixel to know the id of the sphere is under the cursor. This value is then used by the displayed material.'
				);
				raycast1.uiData.setPosition(-100, 0);
				raycast1.p.mode.set(1);
				raycast1.p.mouse.set([0.9737609329446064, 0.25464190981432355]);
				raycast1.p.overrideMaterial.set(true);
				raycast1.p.material.set('../../MAT/meshBasicBuilder_ID_FOR_RAYCAST');
				raycast1.p.pixelColor.set([38, 0, 0]);
				raycast1.p.pixelAlpha.set(1);
				raycast1.p.objectMask.set('*instancesRaycastTarget*');
				raycast1.p.traverseChildren.set(false);
				raycast1.p.tposition.set(false);
				raycast1.params.postCreateSpareParams();
				raycast1.params.runOnSceneLoadHooks();
				return raycast1;
			}
			function create_viewer1(eventsNetwork1: EventsNetworkSopNode) {
				var viewer1 = eventsNetwork1.createNode('viewer');
				viewer1.setName('viewer1');
				viewer1.uiData.setPosition(250, 0);
				viewer1.params.postCreateSpareParams();
				viewer1.params.runOnSceneLoadHooks();
				return viewer1;
			}
			var nodeCook1 = create_nodeCook1(eventsNetwork1);
			var pointer1 = create_pointer1(eventsNetwork1);
			var raycast1 = create_raycast1(eventsNetwork1);
			var viewer1 = create_viewer1(eventsNetwork1);
			raycast1.setInput('trigger', pointer1, 'pointermove');
			raycast1.setInput('updateObjects', nodeCook1, 'each');
			viewer1.setInput('setCss', raycast1, 'hit');
			viewer1.setInput('unSetCss', raycast1, 'miss');
			eventsNetwork1.uiData.setComment('Dive inside here to see how the raycasting is setup');
			eventsNetwork1.uiData.setPosition(-400, 500);
			eventsNetwork1.params.postCreateSpareParams();
			eventsNetwork1.params.runOnSceneLoadHooks();
			return eventsNetwork1;
		}
		function create_instance1(blink_on_hover_with_instances: GeoObjNode) {
			var instance1 = blink_on_hover_with_instances.createNode('instance');
			instance1.setName('instance1');
			instance1.uiData.setPosition(100, 500);
			instance1.flags.display.set(true);
			instance1.p.attributesToCopy.set('instance* id');
			instance1.p.material.set('../MAT/meshStandardBuilder1');
			instance1.params.postCreateSpareParams();
			instance1.params.runOnSceneLoadHooks();
			return instance1;
		}
